import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface NavbarProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
  /**
   * Position of the navigation
   */
  position?: "top" | "bottom";

  fixed?: boolean;

  app?: boolean;
}

export interface NavbarItemProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
}

export interface NavbarSectionProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
  /**
   * Should we push this element to the right
   */
  pullRight?: boolean;
}

export default class Navbar extends NamespaceWrapper<NavbarProps> {
  static propTypes = {//
    "position": PropTypes.oneOf(["top", "bottom"]), //
    "fixed": PropTypes.bool, //
    "app": PropTypes.bool, //
    "className": PropTypes.string, //
  };

  render() {
    const {namespace, position, app, fixed, className, ...props} = this.props;
    const classes = this.classes(
      "Navbar",
      position && "Navbar--" + position,
      app && "Navbar--app",
      fixed && "Navbar--fixed",
      className
    );

    return <div className={classes} {...props} />;
  }
}

export class NavbarItem extends NamespaceWrapper<NavbarItemProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <div className={this.classes("Navbar__item", className)} {...props} />;
  }
}

export class NavbarSection extends NamespaceWrapper<NavbarSectionProps> {
  render() {
    const {namespace, className, pullRight, ...props} = this.props;
    return <div className={this.classes("Navbar__section", (pullRight && "Navbar__section--right"), className)} {...props} />;
  }
}
